<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="150px"
			>
			<el-form-item class="add-item" label="课程名称" prop="kechengmingcheng">
				<el-input v-model="ruleForm.kechengmingcheng" 
					placeholder="课程名称" clearable :disabled=" false  ||ro.kechengmingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="课程分类" prop="kechengfenlei">
				<el-select v-model="ruleForm.kechengfenlei" placeholder="请选择课程分类" :disabled=" false  ||ro.kechengfenlei" >
					<el-option
						v-for="(item,index) in kechengfenleiOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="教师工号" prop="jiaoshigonghao">
				<el-select  @change="jiaoshigonghaoChange" v-model="ruleForm.jiaoshigonghao" placeholder="请选择教师工号">
					<el-option
						v-for="(item,index) in jiaoshigonghaoOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="教师姓名" prop="jiaoshixingming">
				<el-input v-model="ruleForm.jiaoshixingming" 
					placeholder="教师姓名" clearable :disabled=" false  ||ro.jiaoshixingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="上课地点" prop="shangkedidian">
				<el-input v-model="ruleForm.shangkedidian" 
					placeholder="上课地点" clearable :disabled=" false  ||ro.shangkedidian"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="排课时间" prop="paikeshijian">
				<el-input v-model="ruleForm.paikeshijian" 
					placeholder="排课时间" clearable :disabled=" false  ||ro.paikeshijian"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="学生名单" prop="xueshengmingdan">
				<el-input
					type="textarea"
					:rows="8"
					placeholder="学生名单"
					v-model="ruleForm.xueshengmingdan">
					</el-input>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text">更新信息</span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text">取消</span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					kechengmingcheng : false,
					kechengfenlei : false,
					jiaoshigonghao : false,
					jiaoshixingming : false,
					shangkedidian : false,
					paikeshijian : false,
					xueshengmingdan : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					kechengmingcheng: '',
					kechengfenlei: '',
					jiaoshigonghao: '',
					jiaoshixingming: '',
					shangkedidian: '',
					paikeshijian: '',
					xueshengmingdan: '',
				},
				kechengfenleiOptions: [],
				jiaoshigonghaoOptions: [],


				rules: {
					kechengmingcheng: [
					],
					kechengfenlei: [
					],
					jiaoshigonghao: [
					],
					jiaoshixingming: [
					],
					shangkedidian: [
					],
					paikeshijian: [
					],
					xueshengmingdan: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 下载
			download(file){
				window.open(`${file}`)
			},
			// 初始化
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='kechengmingcheng'){
							this.ruleForm.kechengmingcheng = obj[o];
							this.ro.kechengmingcheng = true;
							continue;
						}
						if(o=='kechengfenlei'){
							this.ruleForm.kechengfenlei = obj[o];
							this.ro.kechengfenlei = true;
							continue;
						}
						if(o=='jiaoshigonghao'){
							this.ruleForm.jiaoshigonghao = obj[o];
							this.ro.jiaoshigonghao = true;
							continue;
						}
						if(o=='jiaoshixingming'){
							this.ruleForm.jiaoshixingming = obj[o];
							this.ro.jiaoshixingming = true;
							continue;
						}
						if(o=='shangkedidian'){
							this.ruleForm.shangkedidian = obj[o];
							this.ro.shangkedidian = true;
							continue;
						}
						if(o=='paikeshijian'){
							this.ruleForm.paikeshijian = obj[o];
							this.ro.paikeshijian = true;
							continue;
						}
						if(o=='xueshengmingdan'){
							this.ruleForm.xueshengmingdan = obj[o];
							this.ro.xueshengmingdan = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 获取用户信息
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.jiaoshigonghao!=''&&json.jiaoshigonghao) || json.jiaoshigonghao==0){
							this.ruleForm.jiaoshigonghao = json.jiaoshigonghao;
							this.ro.jiaoshigonghao = true;
						}
						if((json.jiaoshixingming!=''&&json.jiaoshixingming) || json.jiaoshixingming==0){
							this.ruleForm.jiaoshixingming = json.jiaoshixingming;
							this.ro.jiaoshixingming = true;
						}
					}
				});
				this.$http.get('option/kechengfenlei/kechengfenlei', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.kechengfenleiOptions = res.data.data;
					}
				});
				this.$http.get('option/bianchengjiaoshi/jiaoshigonghao', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.jiaoshigonghaoOptions = res.data.data;
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},
			// 下二随
			jiaoshigonghaoChange () {
				this.$http.get('follow/bianchengjiaoshi/jiaoshigonghao?columnValue=' + this.ruleForm.jiaoshigonghao, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						if(res.data.data.jiaoshixingming){
							this.ruleForm.jiaoshixingming = res.data.data.jiaoshixingming
						}
					}
				});
			},

			// 多级联动参数
			// 多级联动参数
			info() {
				this.$http.get(`paikexinxi/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 提交
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`paikexinxi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// 获取uuid
			getUUID () {
				return new Date().getTime();
			},
			// 返回
			back() {
				this.$router.go(-1);
			},
		}
	};
</script>

